<template>
  <div>
    <v-container class="container mt-0" fluid>
      <v-card color="accent" elevation="6">
        <v-row style="margin: 10px">
          <v-col cols="6">
            <h3 class="warning-color">المصاريف</h3>
          </v-col>

          <v-col cols="6" style="text-align: left">
            <v-row>
              <v-col cols="11" style="text-align: left">
                <AddAdministrative @reloadTask="initialData()" style="display: inline-block; margin: 0 5px" />
              </v-col>
              <v-col cols="1">
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn class="mx-2" fab small color="primary" @click="$router.go(-1)" v-bind="attrs" v-on="on">
                      <v-icon dark> mdi-arrow-left </v-icon>
                    </v-btn>
                  </template>
                  <span>رجوع</span>
                </v-tooltip>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card>
    </v-container>
    <v-container class="container mt-0" fluid>
      <v-container class="container" fluid>
        <v-card color="accent" elevation="6">
          <v-card-title class="warning-color">
            المصاريف الادارية
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="mdi-magnify" label="بحث" single-line hide-details solo></v-text-field>
          </v-card-title>
          <v-data-table :headers="headers" :items="ItemsFilter" :items-per-page="10" class="table-content table-warning-color">
            <template v-slot:[`item.index`]="{ index }">
              <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
            </template>

            <template v-slot:[`item.actions`]="{ item }">
              <DeleteItem style="display: inline-block" :id="item.idAdministrativeExpenses" url="administrativeExpenses" @reloadTask="initialData()" />
            </template>
          </v-data-table>
        </v-card>
      </v-container>
    </v-container>
  </div>
</template>

<script>
import AddAdministrative from "./../components/AddAdministrative.vue";
import DeleteItem from "./../components/DeleteItem.vue";

export default {
  components: {
    AddAdministrative,
    DeleteItem,
  },
  data: () => ({
    show: false,
    search: "",
    expenses: [],
    headers: [
      { text: "التسلسل", value: "index" },
      { text: "نوع الصرف", value: "expensesTypeName" },
      { text: "التكلفة", value: "amount" },
      { text: "الملاحظات", value: "note" },
      { text: "بواسطة", value: "userName" },
      { text: "الاجراءات", value: "actions" },
    ],
  }),
  created() {
    this.initialData();
  },
  methods: {
    initialData() {
      let self = this;
      self.$http.get(`${self.$store.state.apiUrl}/projectExpenses/${this.$store.state.projectExpenses.idProject}`).then((res) => {
        console.log(res.data);
        this.expenses = res.data;
        this.show = true;
      });
    },
  },
  computed: {
    ItemsFilter() {
      if (this.search) {
        return this.expenses.filter((item) => {
          return this.search
            .toLowerCase()
            .split(" ")
            .every((v) => item.expensesTypeName.toLowerCase().includes(v));
        });
      } else {
        return this.expenses;
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
