<template>
   <div>
      <v-container class="container mt-0" fluid>
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="6">
                  <h3 class="warning-color">المهندسين</h3>
               </v-col>

               <v-col cols="6" style="text-align: left">
                  <AddAccount
                     style="display: inline-block"
                     @reloadTask="initialData()"
                  />
                  <v-tooltip bottom>
                     <template v-slot:activator="{ on, attrs }">
                        <v-btn
                           class="mx-2"
                           fab
                           small
                           color="primary"
                           @click="$router.push('/details')"
                           v-bind="attrs"
                           v-on="on"
                        >
                           <v-icon dark> mdi-arrow-left </v-icon>
                        </v-btn>
                     </template>
                     <span>اضافة صنف</span>
                  </v-tooltip>
               </v-col>
            </v-row>
         </v-card>
      </v-container>

      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-card-title class="warning-color">
               المهندسين
               <v-spacer></v-spacer>
               <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="بحث"
                  single-line
                  hide-details
                  solo
               ></v-text-field>
            </v-card-title>
            <v-data-table
               :headers="headers"
               :items="ItemsFilter"
               :items-per-page="10"
               class="table-content table-warning-color"
            >
               <template v-slot:item.index="{ item, index }">
                  <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
               </template>

               <!-- <template v-slot:header="props">{{ props }}</template> -->

               <template v-slot:item.dailyWork="{ item, index }">
                  <h5 style="display: inline-block">
                     من : {{ item.fromTime }} &nbsp; \ &nbsp;
                  </h5>
                  <h5 style="display: inline-block">الى : {{ item.toTime }}</h5>
                  <h5>العدد : {{ item.number }}</h5>
               </template>

               <template v-slot:item.actions="{ item }">
                  <EngProject
                     :user="item"
                     :projects="projects"
                     style="display: inline-block"
                  />

                  <DeleteItem
                     style="display: inline-block"
                     :id="item.idUser"
                     url="user"
                     @reloadTask="initialData()"
                  />
               </template>
            </v-data-table>
         </v-card>
      </v-container>
   </div>
</template>

<script>
import AddAccount from "./../components/AddAccount.vue";
import EngProject from "./../components/EngProject.vue";
import DeleteItem from "./../components/DeleteItem.vue";
export default {
   components: {
      AddAccount,
      EngProject,
      DeleteItem,
   },
   data: () => ({
      accounts: [],
      projects: [],
      search: "",
      headers: [
         { text: "التسلسل", value: "index" },
         { text: "اسم المهندس", value: "userName" },
         { text: "كلمة المرور", value: "password" },
         { text: "الاجراءات", value: "actions" },
      ],
   }),

   //    gas - food - credit - transport - general
   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         let self = this;
         self.$http
            .get(`${self.$store.state.apiUrl}/usersRole/3`)
            .then((res) => {
               console.log(res.data);
               self.accounts = res.data;
            });

         self.$http.get(`${self.$store.state.apiUrl}/projects`).then((res) => {
            self.projects = res.data;
         });
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.accounts.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) => item.vehicleType.toLowerCase().includes(v));
            });
         } else {
            return this.accounts;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
