<template>
  <div v-if="show">
    <v-container class="container mt-0" fluid>
      <v-container class="container" fluid>
        <v-card color="accent" elevation="6">
          <v-card-title class="warning-color">
            المشاريع
            <v-spacer></v-spacer>
            <v-text-field v-model="search" append-icon="mdi-magnify" label="بحث" single-line hide-details solo></v-text-field>
          </v-card-title>
          <v-data-table :headers="headers" :items="ItemsFilter" :items-per-page="10" class="table-content table-warning-color">
            <template v-slot:[`item.index`]="{ index }">
              <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
            </template>

            <template v-slot:[`item.actions`]="{ item }">
              <v-tooltip bottom>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn icon v-bind="attrs" v-on="on" @click="setProjectExpenses(item)">
                    <v-icon color="primary"> mdi-arrow-left-drop-circle </v-icon>
                  </v-btn>
                </template>
                <span>التفاصيل</span>
              </v-tooltip>
            </template>
          </v-data-table>
        </v-card>
      </v-container>
    </v-container>
  </div>
</template>

<script>
import AddProjectDetails from "./../components/AddProjectDetails.vue";
import DeleteItem from "./../components/DeleteItem.vue";
import EditDetails from "./../components/projectDetails/editDetails.vue";
export default {
  components: {
    AddProjectDetails,
    DeleteItem,
    EditDetails,
  },
  data: () => ({
    project: null,
    workingDays: [],
    projects: [],
    show: false,
    search: "",
    headers: [
      { text: "التسلسل", value: "index" },
      { text: "اسم المشروع", value: "projectName" },
      { text: "تاريخ بدء المشروع", value: "startDateFormatted" },
      { text: "تاريخ انتهاء المشروع", value: "endDateFormatted" },
      { text: "التكلفة", value: "totalCost" },
      { text: "مكان العمل", value: "workPlace" },
      { text: "الملاحظات", value: "note" },
      { text: "الاجراءات", value: "actions" },
    ],
  }),
  created() {
    let self = this;
    self.initialData();
    //   vehicle
    //   worker
    //   expense
  },
  methods: {
    initialData() {
      let self = this;
      self.$http.get(`${self.$store.state.apiUrl}/projects`).then((res) => {
        console.log(res.data);
        this.projects = res.data;
        this.show = true;
      });
    },
    setProjectExpenses(project) {
      this.$store.state.projectExpenses = project;
      this.$router.push("/expenses-details");
    },
  },
  computed: {
    ItemsFilter() {
      if (this.search) {
        return this.projects.filter((item) => {
          return this.search
            .toLowerCase()
            .split(" ")
            .every((v) => item.projectName.toLowerCase().includes(v));
        });
      } else {
        return this.projects;
      }
    },
  },
};
</script>

<style lang="scss" scoped></style>
