<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>تعديل يوم عمل</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-text-field
                  v-model="item.workPlace"
                  outlined
                  label="مكان العمل"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="item.documentNumber"
                  outlined
                  label="رقم الاستمارة"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-text-field
                  v-model="item.workDuration"
                  outlined
                  label="فترة العمل"
                  :rules="studentRequiredFormRules"
               ></v-text-field>

               <v-select
                  v-model="item.day"
                  :items="days"
                  label="اليوم"
                  outlined
               ></v-select>
               <v-row>
                  <v-col cols="6">
                     <v-select
                        v-model="item.categoryId"
                        :items="$store.state.categories"
                        label="نوع العمل"
                        item-text="categoryName"
                        item-value="idCategory"
                        @change="selectCategory"
                        outlined
                     ></v-select>
                  </v-col>
                  <v-col cols="6">
                     <v-select
                        v-model="item.subCategoryId"
                        :items="subCategories"
                        label="عن"
                        :disabled="disabledSubCategory"
                        item-text="subCategoryName"
                        item-value="idSubCategory"
                        outlined
                     ></v-select>
                  </v-col>
               </v-row>

               <v-menu
                  ref="menu1"
                  v-model="menu1"
                  :close-on-content-click="false"
                  transition="scale-transition"
                  offset-y
                  max-width="290px"
                  min-width="auto"
               >
                  <template v-slot:activator="{ on, attrs }">
                     <v-text-field
                        v-model="dateFormatted"
                        label="التاريخ"
                        persistent-hint
                        prepend-icon="mdi-calendar"
                        outlined
                        v-bind="attrs"
                        @blur="date = parseDate(dateFormatted)"
                        v-on="on"
                     ></v-text-field>
                  </template>
                  <v-date-picker
                     v-model="date"
                     no-title
                     @input="menu1 = false"
                  ></v-date-picker>
               </v-menu>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حفظ </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               v-if="$store.state.userCredentials.roleId != 3"
               icon
               v-bind="attrs"
               @click="show = true"
               v-on="on"
            >
               <v-icon color="primary"> mdi-pencil </v-icon>
            </v-btn>
         </template>
         <span>تعديل</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      item: Object,
   },
   data: (vm) => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      date: new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
         .toISOString()
         .substr(0, 10),
      dateFormatted: vm.formatDate(
         new Date(Date.now() - new Date().getTimezoneOffset() * 60000)
            .toISOString()
            .substr(0, 10),
      ),
      categoryName: "",
      categoryId: "",
      workPlace: "",
      disabledSubCategory: true,
      subCategories: [],
      subCategoryId: "",
      documentNumber: "",
      workDurationFrom: "",
      workDurationTo: "",

      days: [
         "السبت",
         "الاحد",
         "الاثنين",
         "الثلاثاء",
         "الاربعاء",
         "الخميس",
         "الجمعة",
      ],
      day: "",
      reportDate: "",
      menu1: false,
   }),
   methods: {
      saveData() {
         let self = this;

         let data = {
            projectId: self.item.projectId,
            workPlace: self.item.workPlace,
            documentNumber: self.item.documentNumber,
            workDuration: self.item.workDuration,
            day: self.item.day,
            reportDate: self.date,
            subCategoryId: self.item.subCategoryId,
            createdBy: self.$store.state.userCredentials.idUser,
         };

         console.log(data);
         this.$http
            .put(
               `${this.$store.state.apiUrl}/report/${self.item.idReport}`,
               data,
            )
            .then((res) => {
               this.show = false;
               this.$toasted.success("تم اضافه البيانات");
               this.$emit("reloadTask");
            })
            .catch((err) => {
               this.$toasted.error(" حدث خطأ في البيانات");
            });

         console.log(this.item);
      },
      selectCategory() {
         let self = this;
         self.subCategories = this.$store.state.categories.filter(
            (category) => category.idCategory == this.item.categoryId,
         )[0].subCategory;
         self.disabledSubCategory = false;
      },
      formatDate(date) {
         if (!date) return null;

         const [year, month, day] = date.split("-");
         return `${month}/${day}/${year}`;
      },
      parseDate(date) {
         if (!date) return null;

         const [month, day, year] = date.split("/");
         return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`;
      },
   },
   computed: {
      computedDateFormatted() {
         return this.formatDate(this.date);
      },
   },
   watch: {
      date(val) {
         this.dateFormatted = this.formatDate(this.date);
      },
   },
};
</script>

<style lang="scss" scoped></style>
