<template>
   <div>
      <v-container class="container mt-0" fluid>
         <v-card color="accent" elevation="6">
            <v-row style="margin: 10px">
               <v-col cols="10">
                  <h3 class="warning-color">صفحة انواع المصروفات</h3>
               </v-col>

               <v-col cols="2" align="left">
                  <AddExpensesType @reloadTask="initialData()" />
               </v-col>
            </v-row>
         </v-card>
      </v-container>
      <v-container class="container" fluid>
         <v-card color="accent" elevation="6">
            <v-card-title class="warning-color">
               انواع المصروفات
               <v-spacer></v-spacer>
               <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="بحث"
                  single-line
                  hide-details
                  solo
               ></v-text-field>
            </v-card-title>
            <v-data-table
               :headers="headers"
               :items="ItemsFilter"
               :items-per-page="10"
               class="table-content table-warning-color"
            >
               <template v-slot:[`item.isActive`]="{ item }">
                  <v-avatar size="20" color="success" v-if="item.isActive == 1">
                  </v-avatar>

                  <v-avatar size="20" color="error" v-else> </v-avatar>
               </template>

               <template v-slot:[`item.index`]="{ index }">
                  <h4 style="padding: 0 3px">{{ index + 1 }}</h4>
               </template>

               <template v-slot:[`item.actions`]="{ item }">
                  <EditExpensesType
                     style="display: inline-block"
                     :item="item"
                     @reloadTask="initialData()"
                  />
                  <!-- <v-btn icon @click="dialog = false">
                     <v-icon color="primary" @click="deleteItem(item)">
                        mdi-delete
                     </v-icon>
                  </v-btn> -->
               </template>
            </v-data-table>
         </v-card>
      </v-container>
   </div>
</template>

<script>
import AddExpensesType from "./../components/AddExpensesType.vue";
import EditExpensesType from "./../components/EditExpensesType.vue";
import SubCategoryModel from "./../components/SubCategoryModel.vue";
export default {
   components: {
      AddExpensesType,
      EditExpensesType,
      SubCategoryModel,
   },
   data: () => ({
      expensesType: [],
      search: "",
      headers: [
         { text: "التسلسل", value: "index" },
         { text: "نوع المصروف", value: "expensesTypeName" },
         { text: "الاجراءات", value: "actions" },
      ],
   }),
   created() {
      this.initialData();
   },
   methods: {
      initialData() {
         let self = this;
         self.$http
            .get(`${self.$store.state.apiUrl}/expensesTypes`)
            .then((res) => {
               console.log(res.data);
               self.expensesType = res.data;
            });
      },
   },
   computed: {
      ItemsFilter() {
         if (this.search) {
            return this.expensesType.filter((item) => {
               return this.search
                  .toLowerCase()
                  .split(" ")
                  .every((v) =>
                     item.expensesTypeName.toLowerCase().includes(v),
                  );
            });
         } else {
            return this.expensesType;
         }
      },
   },
};
</script>

<style lang="scss" scoped></style>
