<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="primary">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>مشاريع المهندس</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <v-row>
                  <v-col cols="8">
                     <v-select
                        v-model="projectId"
                        item-text="projectName"
                        item-value="idProject"
                        :items="projects"
                        label="المشاريع"
                        outlined
                     ></v-select>
                  </v-col>
                  <v-col cols="4">
                     <v-btn
                        x-large
                        outlined
                        color="success"
                        @click="addUserProject"
                     >
                        <v-icon left> mdi-plus </v-icon>
                        اضافة
                     </v-btn>
                  </v-col>
               </v-row>

               <v-simple-table>
                  <template v-slot:default>
                     <thead>
                        <tr>
                           <th class="text-left">اسم المشروع</th>
                           <th class="text-left">مكان المشروع</th>
                        </tr>
                     </thead>
                     <tbody>
                        <tr v-for="item in projectsEng" :key="item.idProject">
                           <td>{{ item.projectName }}</td>
                           <td>{{ item.workPlace }}</td>
                        </tr>
                     </tbody>
                  </template></v-simple-table
               >
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="saveData"> حسنا </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               class="mx-2"
               small
               icon
               color="primary"
               @click="show = true"
               v-bind="attrs"
               v-on="on"
            >
               <v-icon dark> mdi-sitemap </v-icon>
            </v-btn>
         </template>
         <span>اضافة مشروع</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      user: Object,
      projects: Array,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      projectsEng: [],
      categoryName: "",
      projectId: "",
   }),
   created() {
      this.engProjects();
   },
   methods: {
      addUserProject() {
         let self = this;
         console.log(self.projectId);
         if (self.projectId) {
            let data = {
               userId: self.user.idUser,
               projectId: self.projectId,
            };
            this.$http
               .post(`${this.$store.state.apiUrl}/addProjectResponsible`, data)
               .then((res) => {
                  this.$toasted.success("تم اضافه البيانات");
                  this.engProjects();
               })
               .catch((err) => {
                  this.$toasted.error(" حدث خطأ في البيانات");
               });
         } else {
            this.$toasted.error("الرجاء اختيار مشروع");
         }
      },
      engProjects() {
         let self = this;
         self.$http
            .get(
               `${self.$store.state.apiUrl}/responsibleProjects/${self.user.idUser}`
            )
            .then((res) => {
               console.log(res.data);
               self.projectsEng = res.data;
            });
      },
      saveData() {
         this.show = false;
         this.$emit("reloadTask");
      },
   },
};
</script>

<style lang="scss" scoped></style>
