<template>
   <div>
      <v-dialog v-model="show" persistent max-width="600px" width="600px">
         <v-card>
            <v-toolbar dark color="error">
               <v-btn icon @click="show = false">
                  <v-icon>mdi-close</v-icon>
               </v-btn>
               <v-toolbar-title>حذف العنصر</v-toolbar-title>
               <v-spacer></v-spacer>
            </v-toolbar>

            <v-card-text>
               <div style="margin-top: 10px"></div>

               <h4>هل انت متأكد من حذف العنصر ؟</h4>
               <h4>عند الحذف لا يمكن استعادة البيانات</h4>
            </v-card-text>

            <v-divider></v-divider>

            <v-card-actions>
               <v-btn text @click="deleteItem"> حذف </v-btn>
            </v-card-actions>
         </v-card>
      </v-dialog>
      <v-tooltip bottom>
         <template v-slot:activator="{ on, attrs }">
            <v-btn
               v-if="$store.state.userCredentials.roleId != 3"
               icon
               v-bind="attrs"
               @click="show = true"
               v-on="on"
            >
               <v-icon color="error"> mdi-delete </v-icon>
            </v-btn>
         </template>
         <span>حذف</span>
      </v-tooltip>
   </div>
</template>

<script>
export default {
   props: {
      id: Number,
      url: String,
   },
   data: () => ({
      studentRequiredFormRules: [(v) => !!v || "حقل مطلوب . يرجى ادخال قيمة"],
      show: false,
      categoryName: "",
   }),
   methods: {
      deleteItem() {
         let self = this;

         this.$http
            .delete(`${this.$store.state.apiUrl}/${self.url}/${self.id}`)
            .then((res) => {
               this.show = false;
               this.$toasted.success("تم حذف البيانات");
               this.$emit("reloadTask");
            })
            .catch((err) => {
               if (err.response?.status == 600) {
                  this.$toasted.error(
                     "لايمكن حذف المشروع لارتباطه بحسابات اخرى",
                  );
                  this.show = false;
               } else {
                  this.$toasted.error(" حدث خطأ في البيانات");
                  this.show = false;
               }
            });
      },
   },
};
</script>

<style lang="scss" scoped></style>
